"use client";
import { useEffect } from "react";
import { useAuth } from "@/components/AuthContext";
import { useRouter } from "next/navigation";
import {
  Select,
  SelectContent,
  SelectGroup,
  SelectItem,
  SelectLabel,
  SelectTrigger,
  SelectValue,
} from "@/components/ui/select";
import {
  ArrowUp01,
  ArrowUpRight,
  BookAlertIcon,
  ChartNoAxesGanttIcon,
  UserIcon,
} from "lucide-react";
import Cards from "@/components/admins/dashboard/Cards";
import LineBarDashboard from "@/components/admins/dashboard/LineBarDashboard";
import TopCategories from "@/components/admins/dashboard/TopCategories";

export default function Page() {
  const { isAuthenticated, user } = useAuth();
  const router = useRouter();
  useEffect(() => {
    if (!isAuthenticated) {
      router.push("/login");
    }
    if (user?.role == "user") {
      router.push("/");
    }
  }, [isAuthenticated]);

  const handleOnChange = (value: string) => {
    console.log(value);
  };
  return (
    <>
      <div className="w-full px-2 lg:px-12 pb-10">
        <div className="flex justify-between items-center  py-12">
          <span className="text-[28px] font-normal">Dashboard</span>
          <div>
            <Select onValueChange={handleOnChange}>
              <SelectTrigger className="w-[180px] bg-white rounded-lg py-6">
                <SelectValue placeholder="Showing:" />
              </SelectTrigger>
              <SelectContent>
                <SelectGroup>
                  <SelectLabel></SelectLabel>
                  <SelectItem value="this_year">This Year</SelectItem>
                  <SelectItem value="last_30_days">Last 30 days</SelectItem>
                  <SelectItem value="last_7_days">Last 7 days</SelectItem>
                </SelectGroup>
              </SelectContent>
            </Select>
          </div>
        </div>
        <div className="flex flex-col gap-8  lg:flex-row">
          <div className="flex-1 lg:flex-2/3 flex flex-col  gap-10 px-2 lg:px-0">
            <Cards />
            <LineBarDashboard />
          </div>
          <div className="flex-1 lg:flex-1/3 px-2 lg:px-0">
            <TopCategories />
          </div>
        </div>
        <Tabs defaultValue="account" className="w-[400px]">
          <TabsList>
            <TabsTrigger value="account">Account</TabsTrigger>
            <TabsTrigger value="password">Password</TabsTrigger>
          </TabsList>
          <TabsContent value="account">
            Make changes to your account here.
          </TabsContent>
          <TabsContent value="password">Change your password here.</TabsContent>
        </Tabs>
      </div>
    </>
  );
}
